<template>
        <v-card class="fill-height justify-center">
            

            <v-row justify="start" align="start" no-gutters>
                <v-card-title class="form-card-heading">
                    Update an entry:
                </v-card-title>
                <v-btn
                    style="background:white;"                    
                    text
                    icon
                    depressed
                    color="#116466"
                    @click.stop="dialog=true"
                >
                    ?
                </v-btn>

            </v-row>

            <v-dialog
                v-model="dialog"
                max-width="400"
            >
                <v-card class="dialog-font-color">
                    <v-card-title>
                        What to do?
                    </v-card-title>

                    <v-card-text>
                        Enter the desired date whose entry you want to update. Then click on 'FIND ENTRY'. Change whichever values you want then hit 'UPDATE ENTRY'
                    </v-card-text>

                    <v-card-actions>
                        <v-spacer></v-spacer>
                            <!-- class="dialog-font-color" -->
                        <v-btn
                            text
                            color="#116466"
                            style="background:white;"
                            @click="dialog=false"
                        >
                            Close
                        </v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>

            <v-form ref="form">

                <v-row align="center" justify="center">
                    <v-col cols="8" md="3" class="center-element">
                        <v-text-field
                            v-model="date"
                            label="YYYY-MM-DD"
                            :counter=10
                            type="string"
                            required
                        ></v-text-field>
                    </v-col>
                    <v-col cols="8" md="3" class="center-element">
                        <v-btn 
                        text
                        depressed
                        color="#116466"
                        style="background: white;"
                        class="mx-5 my-2"
                        @click="getEntryFormBasedOnDate">
                            Find Entry
                        </v-btn>
                    </v-col>
                </v-row>

                <v-expand-transition>
                    <v-row v-if='isEntryLoaded' align="center" justify="center">
                        <v-col cols="8" md="3">
                            <v-text-field
                                v-model.number="travel"
                                label="traveling?"
                                :counter=2
                                type="number"
                                required
                            ></v-text-field>

                            <v-text-field
                                v-model.number="exercise"
                                label="exercising?"
                                :counter=2
                                type="number"
                                required
                            ></v-text-field>

                            <v-text-field
                                v-model.number="on_phone"
                                label="on the phone?"
                                :counter=2     
                                type="number"                           
                                required
                            ></v-text-field>
                        </v-col>

                        <v-col cols="8" md="3" align="end">
                            <v-text-field
                                v-model.number="sleep"
                                label="sleeping?"
                                :counter=2   
                                type="number"                             
                                required
                            ></v-text-field>
                            
                            <v-text-field
                                v-model.number="on_computer"
                                label="on the computer?"
                                :counter=2    
                                type="number"                            
                                required
                            ></v-text-field>

                            <v-text-field
                                v-model.number="games"
                                label="playing games?"
                                :counter=2    
                                type="number"                            
                                required
                            ></v-text-field>
                        </v-col>
                    </v-row>
                </v-expand-transition>

                <v-row align="center" justify="center">
                    <v-col cols="8" sm="8">
                        <v-card-text :class="setMessageColor" class="center-element">
                            {{message}}
                        </v-card-text>
                    </v-col>
                    
                    <v-col v-if='isEntryLoaded' cols="8" sm="4">
                        <v-btn 
                        text
                        depressed
                        color="#116466"
                        style="background: white;"
                        class="mx-5 my-2"
                        @click="isDataModified">
                            Update Entry
                        </v-btn>
                    </v-col>
                </v-row>

            </v-form>
        </v-card>
</template>

<script>
// @ is an alias to /src
// import Services from '../../services/services';
const { ValidateDate, getCorrectDateFromUser, isSmallerThan }   = require('../../utils/utils');
// const { getEntryByDate } = require('../../utils/databaseutils.js');

export default {
    name: 'FormTimeSpent',
    data () {
        return {
            date:                null,
            sleep:               null,
            travel:              null,
            exercise:            null,
            on_phone:            null,
            on_computer:         null,
            games:               null,
            somethingelse:       0,
            total:               0,
            formerEntry:         {},
            isSuccess:           null,
            isEntryLoaded:       false,
            dialog:              false,
            message:             '',
            messageResponses: {
                invalidHours:    "The total number of hours must be between 0 and 24",
                success:         "Entry was updated",
                entryNotFound:   "Entry was not found. Double check your input",
                invaliddate:     "Date entered was invalid",
                alreadyPosted:   "Already posted for today, wait until tomorrow",
                dataNotModified: "Entry is unchanged or invalid",
                error:           "Something went wrong",
                invalidInputs:   "Check that all fields have values",
            }
        }
    },
    computed: {
        setMessageColor() {
            return this.isSuccess ? 'form-success' : 'form-failed';
        },
    },
    methods: {
        getEntryFormBasedOnDate() {
            // Checking if the text fields have values
            if (this.date == null) {
                this.isSuccess=false;  
                this.message = this.messageResponses.invalidInputs;
                return;
            }

            if (!this.validateDate())
            {
                this.isSuccess = false;
                this.message = this.messageResponses.invaliddate;

                this.isEntryLoaded = false;
            }
            else
            {
                this.isSuccess = true;  this.message = null;

                this.date           = getCorrectDateFromUser(this.date);
                const doesExist     = this.doesEntryExist(this.date);
                
                if (doesExist)
                {
                    const entryForm = this.$store.getters['bbttDatabase/getDatabase'][this.date];

                    // Displaying the rest of the form if the response is valid
                    this.isEntryLoaded = true;    
                    this.sleep         = entryForm.sleep;
                    this.travel        = entryForm.travel;
                    this.exercise      = entryForm.exercise;
                    this.on_phone      = entryForm.on_phone;
                    this.on_computer   = entryForm.on_computer;
                    this.games         = entryForm.games;
      
                    this.formerEntry   = entryForm;
                }
                else
                {
                    this.isSuccess=false;  
                    this.isEntryLoaded = false;               
                    this.message = this.messageResponses.entryNotFound; 
                }
            }
        },
        validateDate() {
            return ValidateDate(this.date);
        },
        async isDataModified() {              
            // Checking if the text fields have values
            if (this.sleep.length<1 || this.travel.length<1 || this.exercise.length<1 || this.on_phone.length<1 || this.on_computer.length<1 || this.games.length<1) {
                this.isSuccess=false;  
                this.message = this.messageResponses.invalidInputs;
                return;
            }

            // Validating date again so that if the user removes the date, they cannot add sql inject code
            var isDateValid = ValidateDate(this.date);
            var currentInput = [this.sleep, this.travel, this.exercise, this.on_phone, this.on_computer, this.games ];
            var formerEntry =  [this.formerEntry.sleep, this.formerEntry.travel, this.formerEntry.exercise, this.formerEntry.on_phone, this.formerEntry.on_computer, this.formerEntry.games];

            // Checking if the new input is the same as the current existing entry and if the input is null or not
            var isEqual = formerEntry.every( (elem, index) => (elem === currentInput[index] || currentInput[index] === null) );

            if (!isEqual && isDateValid)  { this.isSuccess=true; this.message=null; await this.isTotalHoursValid(); }
            else                          { this.isSuccess=false;  this.message = this.messageResponses.dataNotModified; }
        },
        async isTotalHoursValid() {
            this.total = Number(this.sleep) + Number(this.travel) + Number(this.exercise) 
                        + Number(this.on_phone) + Number(this.on_computer) + Number(this.games);

            // If the total number of hours is not between [0, 24]
            if (this.total > 24 || this.total < 0)  { 
                this.isSuccess = false;
                this.message =   this.messageResponses.invalidHours;
                throw new Error("Hours don't make sense");
            }

            // Placing unallocated hours into one category
            this.somethingelse = 24 - this.total; 

            const res = this.updateEntry();
            if (res == 201) 
            {
                console.log(res);
                this.isSuccess = true;
                this.message   = this.messageResponses.success;
            }
            else
            {
                this.isSuccess = false;
                this.message   = this.messageResponses.error;
            }
            // .then( res => {
            //     console.log(res);
            //     this.isSuccess = true;
            //     this.message   = this.messageResponses.success;
            // })
            // .catch( error => {
            //     console.log(error);
            //     this.isSuccess = false;
            //     this.message   = this.messageResponses.error;
            // });
        },
        updateEntry() {
            // Getting data ready to send
            const form = {
                sleep:         this.sleep,
                travel:        this.travel,
                exercise:      this.exercise,
                on_phone:      this.on_phone,
                on_computer:   this.on_computer,
                games:         this.games,
                somethingelse: this.somethingelse
            }
            
            // Adding the form to the store
            var date = this.date; 
            let recentDates = this.$store.getters['bbtt/getDates'];
            // recentDates.sort().reverse();
            recentDates.sort();

            // Pushing the form data to the database
            // return await Services.updateEntry({ date: date, updatedForm: form });
            // const response = await this.updateDatabase(form);
            const response = this.updateDatabase(form);
            
            
            // for(let i = 0; i < recentDates.length; i++) 
            // {
            //     console.log(recentDates[i]);
            // }

            if (isSmallerThan(recentDates[0], date)) 
            {
                recentDates.reverse();
                this.$store.dispatch('bbtt/addNewTimeSpentEntryToFront', { date: date, data: form });
                // this.$store.dispatch('bbtt/setDataFromLastSevenDays'); 

                // recentDates.sort().reverse();
                // for(let i = 0; i < recentDates.length; i++) 
                // {
                //     console.log(recentDates[i]);
                // }


                let lineGraphData = this.$store.getters['bbtt/getWeekOfData'];                        
                const index       = recentDates.indexOf(this.date);
                
                const lineGraphDatalength = Object.keys(lineGraphData).length;

                for(let i = 0; i < lineGraphDatalength; i++) 
                {
                    const j = Object.keys(lineGraphData)[i];
                    lineGraphData[j][index] = form[j];
                }

                
                // for(let i = 0; i < lineGraphDatalength; i++) 
                // {
                //     const j = Object.keys(lineGraphData)[i];
                //     console.log(lineGraphData[j]);
                // }
                this.$store.dispatch('bbtt/updateLineChartData', lineGraphData);
            }

            return response;

        },
        updateDatabase(newEntry) {
            // return new Promise( (resolve, reject) => {
            try {
                let database = this.$store.getters['bbttDatabase/getDatabase'];
                
                database[this.date] = newEntry;

                this.$store.dispatch('bbttDatabase/updateDatabase', database);
                return 201;
            }
            catch(err) {
                console.log(err);
                return err;
            }
            // });
        },
        doesEntryExist(date) {
            // return new Promise( (resolve, reject) => {
            const database = this.$store.getters['bbttDatabase/getDatabase'];
            if (Object.prototype.hasOwnProperty.call(database, date)) 
            {
                return true;
            }
            console.log("404 ENTRY NOT FOUND");
            return false;
        }
    },
}
</script>

<style scoped>
.form-success { color: #116466; }
.form-failed  { color: red;     }

.form-card-heading {
    color:#116466; /* !IMPORTANT; */
    text-align: left;
    font-size: 2rem;
    margin-bottom: 0.4rem;
}
.form-card-subheading {
    color:#116466 !IMPORTANT;
    text-align: left;
    font-size: 1rem;
}
.dialog-font-color {
    color:#116466 !IMPORTANT;
}
.form-border {
    border-left: 2px solid #116466; 
    border-bottom: 2px solid #116466; 
    border-right: 2px solid #116466;
}
